.section {
  padding: 15px;
  width: 40vw;
  height: 50vh;
  /* background: linear-gradient(45deg, rgb(82, 76, 76), rgb(137, 185, 137)); */
  border-radius: 40px;
  box-shadow: 5px 5px 5px rgb(2, 63, 27);
}
.header {
  display: flex;
  justify-content: space-between;
}
.section h2 {
  margin: 0;
  color: rgb(9, 9, 121);
}
.questions li {
  width: max-content;
  cursor: pointer;
  transition: 0.5s;
}
.questions li:hover {
  transform: scale(1.1, 1.1);
}

.scale {
  margin-top: 5px;
  width: 95%;
  height: 5px;
}
.scaleProgress {
  height: 100%;
  width: 10%;
  border-radius: 10px;
  background: linear-gradient(90deg, blue, rgb(128, 0, 96));
  transition-duration: 1s;
}

@media (max-width: 500px) {
  .section {
    width: 90vw;
  }
  .questions li {
    margin-top: 2vh;
    font-size: 18px;
  }
}
