.buttons {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.buttons button {
  width: 250px;
  height: 50px;
  font-size: 18px;
  border-radius: 15px;
  cursor: pointer;
  transition: 0.5s;
  border: none;
  color: aliceblue;
}

.buttons button:nth-child(1) {
  background-color: green;
  box-shadow: 0 0 10px rgb(8, 85, 8);
}
.buttons button:nth-child(2) {
  background-color: blue;
  box-shadow: 0 0 10px rgb(3, 3, 85);
}
.buttons button:nth-child(3) {
  background-color: brown;
  box-shadow: 0 0 10px rgb(82, 8, 8);
}

.buttons button:hover {
  transform: scale(1.2, 1.2);
  color: black;
  background-color: aliceblue;
}

.image {
  max-width: 90%;
}
