.App-header,
.App {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.finalAnswers {
  border: 1px solid wheat;
  padding: 10px 20px;
  width: 30vw;
  display: flex;
  justify-content: space-between;
}
.right {
  color: green;
}
.notRight {
  color: red;
}
.App-header button {
  margin-top: 2vh;
  padding: 15px 10px;
  font-size: 15px;
  border: none;
  border-radius: 15px;
  background-color: bisque;
  color: black;
  cursor: pointer;
}

.textResultGood {
  color: green;
}
.textResultBad {
  color: red;
}

@media (max-width: 700px) {
  .finalAnswers {
    width: 80vw;
    font-size: 15px;
  }
}
